import React from 'react';
import Link from 'gatsby-link';
import styled from 'styled-components';
import SongList from '../components/songlist';
import { availableLanguages } from '../utils/constants';
import getLocale from '../utils/lang';

// TRASH START
import heartheart from '../assets/heartheart.jpg';
import mepng from '../assets/me.png';
// TRASH END

const Container = styled('div')`
  width: 100%;
`;

const TrashContainer = styled('div')`
  width: 100%;
  height: 300px;
  overflow: hidden;
  z-index: -2;

  img {
    position: absolute;
    margin: 0;
  }

  img#me {
    right: 23px;
  }

  img#me2 {
    top: 120px;
    transform: rotate(49deg);
    right: 128px;
  }
`;

export default props => {
  const { data } = props;
  return <Container>
    <TrashContainer>
      <img id="me" src={mepng}/>
      <img src={heartheart}/>
      <img id="me2" src={mepng}/>
    </TrashContainer>
    <SongList data={data}/>
  </Container>;
};

// TODO: Gatsby bug to keep in mind https://github.com/gatsbyjs/gatsby/issues/6916
export const query = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            artist
            language
          }
          fields {
            slug
          }
          excerpt(pruneLength: 140)
        }
      }
    }
  }
`;



